.chart__ygroup .year {
    stroke: #fff;
    stroke-width: 1px;
    fill: transparent;
    pointer-events: none;
    -webkit-transition: r 1000ms 300ms ease-in-out, opacity 200ms ease;
    transition: r 1000ms 300ms ease-in-out, opacity 200ms ease;
    stroke-dasharray: 6000px;
    stroke-dashoffset: 100%;
    opacity: 0.4; }

.chart__ygroup .year__hover {
      stroke: transparent;
      stroke-width: 16px;
      fill: transparent;
      pointer-events: stroke;
      -webkit-transition: r 1000ms 300ms ease-in-out;
      transition: r 1000ms 300ms ease-in-out; }

.chart__ygroup .year__title {
      display: none;
      fill: white;
      text-anchor: middle;
      font-size: 32px;
      font-family: 'dharma-gothic-m', 'webfontregular', Helvetica, Arial, sans-serif; }

.chart__ygroup .item {
    cursor: pointer;
    -webkit-transition: opacity 200ms ease 100ms;
    transition: opacity 200ms ease 100ms; }

.chart__ygroup .item__dot {
      pointer-events: none;
      stroke: none;
      display: none; }

.chart__ygroup .item__doty {
      pointer-events: none;
      stroke: none; }

.chart__ygroup .item__circle {
      stroke: white;
      stroke-width: 1px;
      opacity: 0;
      fill: #050505; }

.chart__ygroup .item__line {
      stroke: white;
      display: none;
      fill: transparent; }

.chart__ygroup .item__text {
      fill: white;
      display: none;
      font-size: 18px; }

.chart__ygroup .item__date {
      display: none;
      font-size: 18px; }

.chart__ygroup .item:hover .item__line, .chart__ygroup .item:hover .item__text, .chart__ygroup .item:hover .item__date, .chart__ygroup .item:hover .item__dot {
      display: block; }

.chart__ygroup .item:hover .item__doty {
      display: none; }

.chart__ygroup .item--text .item__line, .chart__ygroup .item--text .item__circle {
      stroke: #fdc12c; }

.chart__ygroup .item--text .item__date, .chart__ygroup .item--text .item__doty {
      fill: #fdc12c; }

.chart__ygroup .item--photo .item__line, .chart__ygroup .item--photo .item__circle, .chart__ygroup .item--slider .item__line, .chart__ygroup .item--slider .item__circle {
      stroke: #e73c5d; }

.chart__ygroup .item--photo .item__date, .chart__ygroup .item--photo .item__doty, .chart__ygroup .item--slider .item__date, .chart__ygroup .item--slider .item__doty {
      fill: #e73c5d; }

.chart__ygroup .item--shortaudio .item__line, .chart__ygroup .item--shortaudio .item__circle {
      stroke: #2abbb2; }

.chart__ygroup .item--shortaudio .item__date, .chart__ygroup .item--shortaudio .item__doty {
      fill: #2abbb2; }

.chart__ygroup .item--pdf .item__line, .chart__ygroup .item--pdf .item__circle {
      stroke: #bc7871; }

.chart__ygroup .item--pdf .item__date, .chart__ygroup .item--pdf .item__doty {
      fill: #bc7871; }

.chart__ygroup .item--poem .item__line, .chart__ygroup .item--poem .item__circle {
      stroke: #d8643f; }

.chart__ygroup .item--poem .item__date, .chart__ygroup .item--poem .item__doty {
      fill: #d8643f; }

.chart__ygroup .item--goodaudio .item__line, .chart__ygroup .item--goodaudio .item__circle {
      stroke: red; }

.chart__ygroup .item--goodaudio .item__date, .chart__ygroup .item--goodaudio .item__doty {
      fill: red; }

.chart__ygroup .item--video .item__line, .chart__ygroup .item--video .item__circle {
      stroke: #916f8a; }

.chart__ygroup .item--video .item__date, .chart__ygroup .item--video .item__doty {
      fill: #916f8a; }

.chart__ygroup:hover .year {
    opacity: 1; }

.chart__ygroup:hover .year__title {
      display: block; }

.chart__ygroup:hover .item__dot {
    display: block; }

.chart__ygroup:hover .item__doty {
    display: none; }

.chart__ygroup:hover .item__circle {
    opacity: 1; }

.chart .biggroup rect {
    cursor: pointer;
    -webkit-transition: opacity 200ms ease;
    transition: opacity 200ms ease; }

.chart .biggroup text {
    fill: white;
    font-size: 24px;
    cursor: pointer;
    -webkit-transition: opacity 200ms ease;
    transition: opacity 200ms ease; }

.chart .biggroup__item:hover rect,
  .chart .biggroup__item:hover text {
    opacity: 0.6; }

.chart .biggroup__title {
    font-size: 16px !important;
    fill: #fdc12c !important; }

.chart .startyear {
    stroke: white;
    opacity: 0.4; }

@media screen and (min-width: 860px) {
  .chart__metawrap {
    display: block;
    pointer-events: all; }
  .chart__wrap {
    height: 100vh;
    overflow: hidden; } }

/**
* ANIMATIONS
*/

@-webkit-keyframes rotation {
  0% {
    stroke-dasharray: 0;
    stroke-dashoffset: 100%; }
  100% {
    stroke-dasharray: 6000px;
    stroke-dashoffset: 0; } }

@keyframes rotation {
  0% {
    stroke-dasharray: 0;
    stroke-dashoffset: 100%; }
  100% {
    stroke-dasharray: 6000px;
    stroke-dashoffset: 0; } }
